<template>
  <div class="login" :style="mark" >
    <div class="bg"></div>
    <div class="main">
      <el-row>
        <el-col :span="12" style="background: #F2695A;height: 420px;margin-top: -10px;border-radius: 5px;box-shadow: 2px 0 2px #DCDFE6">
          <div class="left">
            <div class="img">
              <i class="el-icon-reading"></i>
            </div>
            <h3>书店后台管理系统</h3>
            <p>书籍是人类进步的阶梯！</p>
          </div>
        </el-col>
        <el-col :span="10">
          <div class="right">
            <el-form ref="loginForm" :model="loginForm" class="elform">
              <el-form-item>
                <el-input prefix-icon="fa fa-user" v-model="loginForm.userName" type="text" autocomplete="off" placeholder="用户名"></el-input>
              </el-form-item>
              <el-form-item>
                <el-input prefix-icon="el-icon-lock" show-password v-model="loginForm.userPwd" type="password" autocomplete="off" placeholder="密码"></el-input>
              </el-form-item>
              <el-form-item>
                <el-button class="btu-all" type="primary" size="medium" @click="clickLogin" >登录</el-button>
              </el-form-item>
            </el-form>
          </div>
        </el-col>
      </el-row>
    </div>
  </div>
</template>

<script>
  const height1 = window.innerHeight;
  export default {
    name: "login",
    data() {
      return {
        mark: {height: height1 + 'px'},
        loginForm:{
          userName:"",
          userPwd:""
        },
        result_Name:'www',
        result_uPwd:'123456',
        admis:[]
      }
    },
    methods:{
      clickLogin() {
        if (this.loginForm.userName!==""&& this.loginForm.userPwd!==""){
            var success = false;
            var len = this.admis.length
            for (var i=0;i<len;i++) {
              if (this.loginForm.userName == this.admis[i].username && this.loginForm.userPwd == this.admis[i].userpwd) {
                var user = [this.loginForm.userName, this.loginForm.userPwd];
                localStorage.setItem("user", JSON.stringify(user));
                this.$router.push("/system_home");
                break;
              }
              if (i===(len-1)){
                success = true
              }
            }
            if (success){
              alert("账号或密码错误");
            }
        }else if (this.loginForm.userName=="") {
          alert("账号不能为空")
        } else{
          alert("密码不能为空")
        }
      }
    },
    mounted(){
      var that_ = this;
      this.$axios.get('http://localhost:3000/db')
        .then(function (response) {
          that_.admis = response.data.administrations
        })
        .catch(function (error) { // 请求失败处理
          console.log(error);
        });
    }
  }
</script>

<style>
  .login {
    position: relative;
    width: 100%;
    margin: -1px 0;
    overflow: hidden;
    display: flex;
    flex-wrap: wrap;
    align-content: center;
    background: #000;
  }
  .bg{
    position: absolute;
    width: 100%;
    height: 100%;
    opacity: 0.9;
    background: url('../assets/login_bk.jpg') fixed center center;
    background-size:100% 100%;
    filter:blur(3px);
  }
  .login .main{
    position: relative;
    margin: -80px auto 0;
    width: 50%;
    height: 400px;
    background: #ffffff;
  }
  .login .el-col{
    padding: 40px;
  }

  .login .left{
    height: 270px;
    text-align: center;
    color: #fff;
  }
  .login .left .img{
    margin: 30px auto 40px;
    height: 150px;
    width: 150px;
    /*border-radius: 50%;*/
    overflow: hidden;
  }
  .login .left .img image{
    width: 100%;
    height: 100%;
  }
  .login .left .img i{
    color: #ffffff;
    font-size: 150px;
  }
  .login .left h3{
    letter-spacing: 3px;
  }
  .login .left p{
    margin-top: 80px;
    font-size: 12px;
  }
  .login .right{
    margin-top: 80px;
    height: 220px;
  }

</style>



