<template>
  <el-container>
    <el-header style="padding: 0;">
      <div class="content">
        <div class="left">
          <div class="nav_log">
            <i class="el-icon-truck"></i>
            <p>书店后台管理系统</p>
          </div>
        </div>
        <div class="right">
          <el-row>
            <el-col :span="18">
              <el-input placeholder="请输入内容"  maxlength="120" :show-word-limit="showWordLimit"
                        class="input-with-select inline-input" :class="searchChange?'':'inputwidth'" @focus="searchChangeFocus"
                        @blur="searchChangeBlur()"
                        v-model="inputsearch1">
                <el-button slot="prepend" icon="el-icon-search"></el-button>
              </el-input>
            </el-col>
            <el-col :span="2">
              <el-dropdown trigger="click">
                <span class="el-dropdown-link">
                  <i class="el-icon-message-solid"></i>
                  <i class="el-icon-arrow-down el-icon--right"></i>
                </span>
                <el-dropdown-menu>
                  <el-dropdown-item icon="el-icon-circle-plus-outline">新消息</el-dropdown-item>
                </el-dropdown-menu>
              </el-dropdown>
            </el-col>
            <el-col :span="2" class="change-icon-class">
              <el-dropdown trigger="click">
                <span class="el-dropdown-link">
                  <i class="fa fa-envelope-o"></i>
                  <i class="el-icon-arrow-down el-icon--right"></i>
                </span>
                <el-dropdown-menu slot="dropdown">
                  <el-dropdown-item icon="fa fa-envelope bg-red"><span style="font-size: 12px">你有3条新消息</span>
                  </el-dropdown-item>
                  <el-dropdown-item icon="fa fa-facebook-square"><span style="font-size: 12px">你有6条新消息</span>
                  </el-dropdown-item>
                  <el-dropdown-item icon="fa fa-twitter bg-skyblue"><span style="font-size: 12px">你有2条新消息</span>
                  </el-dropdown-item>
                </el-dropdown-menu>
              </el-dropdown>
            </el-col>
            <el-col :span="2">
              <el-dropdown trigger="click">
                <span class="el-dropdown-link">
                  <i class="fa fa-user-circle"></i>
                  {{localUsers}}
                  <i class="el-icon-arrow-down el-icon--right"></i>
                </span>
                <el-dropdown-menu slot="dropdown">
                  <el-dropdown-item icon="fa fa-telegram">个人信息</el-dropdown-item>
                  <el-dropdown-item icon="fa fa-cog fa-fw">设置</el-dropdown-item>
                  <el-dropdown-item icon="fa fa-sign-out" divided><span  @click="loginOut()">退出登录</span></el-dropdown-item>
                </el-dropdown-menu>
              </el-dropdown>
            </el-col>
          </el-row>
        </div>
      </div>
    </el-header>
    <el-container style="position: relative;">
      <div style="position: fixed;top: 70px;z-index:6;background: #EDEDED;" :style="{'height':mark}">
        <el-aside style="width: 250px;">
          <el-menu default-active="1"
                   class="el-menu-vertical-demo"
                   background-color="#EDEDED"
                   text-color="#606266"
                   active-text-color="#fff"
                   :collapse="isCollapse"
                   :router="true">
            <!--公告管理-->
            <el-menu-item index="1">
              <i class="fa fa-bullhorn"></i>
              <span>公告管理</span>
            </el-menu-item>
            <!--用户管理-->
            <el-submenu index="2">
              <template slot="title">
                <i class="fa fa-user-o"></i>
                <span>用户管理</span>
              </template>
              <el-menu-item-group>
                <el-menu-item index="user-ordinaryUser">普通用户</el-menu-item>
                <el-menu-item index="user-administrations">管理员</el-menu-item>
                <el-menu-item index="user-addUser">添加用户</el-menu-item>
              </el-menu-item-group>
            </el-submenu>
            <el-submenu index="3">
              <template slot="title">
                <i class="el-icon-s-order"></i>
                <span>订单管理</span>
              </template>
              <el-menu-item-group style="overflow: hidden;">
                <el-menu-item index="orderDfh">物流管理</el-menu-item>
                <el-menu-item index="orderYfh">订单处理</el-menu-item>
              </el-menu-item-group>
            </el-submenu>
            <!--图书管理-->
            <el-submenu index="4">
              <template slot="title">
                <i class="el-icon-notebook-2"></i>
                <span>图书管理</span>
              </template>
              <el-menu-item-group>
                <el-menu-item index="booksM-1">图书列表</el-menu-item>
                <el-menu-item index="booksM-2">推荐列表</el-menu-item>
                <el-menu-item index="booksM-3">最新列表</el-menu-item>
                <el-menu-item index="booksM-4">添加图书</el-menu-item>
                <el-menu-item index="booksM-5">促销列表</el-menu-item>
              </el-menu-item-group>
            </el-submenu>
            <!--类目管理-->
            <el-submenu index="5">
              <template slot="title">
                <i class="fa fa-book"></i>
                <span>类目管理</span>
              </template>
              <el-menu-item-group>
                <el-menu-item index="5-1">类目列表</el-menu-item>
                <el-menu-item index="5-2">添加类目</el-menu-item>
              </el-menu-item-group>
            </el-submenu>
            <!--数据统计-->
            <el-submenu index="6">
              <template slot="title">
                <i class="el-icon-s-data"></i>
                <span>数据统计</span>
              </template>
              <el-menu-item-group>
                <el-menu-item index="OrderStatistic">订单统计</el-menu-item>
                <el-menu-item index="SaleStatistic">销量统计</el-menu-item>
                <el-menu-item index="dataStatistics-3">用户来源统计</el-menu-item>
                <el-menu-item index="AgentSaleStatistic">代理商销量统计</el-menu-item>
              </el-menu-item-group>
            </el-submenu>
          </el-menu>
        </el-aside>
      </div>

      <el-main style="position: relative; z-index:1;margin: 70px 0 0 250px;overflow:hidden;">
        <router-view></router-view>
      </el-main>
    </el-container>
  </el-container>
</template>

<script>
  const height2 = window.innerHeight - 70;
  if(localStorage.getItem('user')){
    var localname = localStorage.getItem('user').split(",")[0].slice(2, -1);
  }
  export default {
    name: 'system_home',
    data() {
      return {
        inputsearch1: "",
        inputSearch: true,
        isCollapse: false,
        searchChange: false,
        mark: height2 + 'px',
        localUsers: localname,
        showWordLimit: false,
        // 激活输入框列出输入建议
        restaurants: [],
        state1: '',
      }
    },
    methods: {
      searchChangeFocus() {
        this.searchChange = true;
        this.showWordLimit = true
      },
      searchChangeBlur() {
        if (this.inputsearch1 == "") {
          this.searchChange = false;
          this.showWordLimit = false
        }
      },
      loginOut(){
        localStorage.setItem("user",JSON.stringify(" "))
        console.log("您已退出,当前user:"+localStorage.getItem("user"))
        this.$router.push({
          path:"/login"
        })
      }
    },
    created() {
      const loading = this.$loading({
        lock: true,
        text: '加载中...',
        spinner: 'el-icon-loading',
        background: 'rgba(0, 0, 0, 0.7)'
      });
      setTimeout(() => {
        loading.close();
      }, 2000);
    }

  }
</script>
<style>
  /*头部*/
  .el-header {
    position: fixed;
    z-index: 7;
    height: 70px !important;
    width: 100%;
    padding: 0;
    line-height: 70px;
    font-size: 24px;
    text-align: center;
    background: #fff;
  }

  .el-header .content {
    position: fixed;
    width: 100%;
    box-shadow: 0 2px 12px 0 rgba(0, 0, 0, 0.1);
  }

  .el-header .left {
    float: left;
    width: 250px;
  }

  .el-header .right {
    float: left;
    width: 80%;
    margin-top: -4px;
    padding: 0 1%;
  }

  .el-header .nav_log {
    height: 100%;
    width: 100%;
    background: #F36A5A;
    font-size: 16px;
    letter-spacing: 4px;
    color: #fff;
  }

  .el-header .nav_log p {
    display: inline-block;
  }

  .el-header .el-button.el-button--default {
    background-color: #fff;
  }

  .el-header .right .el-col .input-with-select input {
    transition: all 0.5s !important;
  }

  .el-header .right .el-col .inputwidth input {
    width: 12%;
  }

  .el-aside .is-active {
    background: #409EFF !important;
  }

  i.fa {
    margin-right: 5px;
    width: 24px;
    text-align: center;
    font-size: 18px !important;
    vertical-align: middle;
  }

  .el-header .right span {
    color: #909399;
    font-size: 12px;
  }

</style>



