<template>
  <el-container>
    <el-tabs type="card" v-model="editableTabsValue" @tab-remove="removeTab" style="width: 100%">
      <el-tab-pane label="普通用户">
        <div>
          <template>
            <el-table ref="multipleTable"
                      :data="tableData.slice((currentPage-1)*pageSize,currentPage*pageSize)"
                      tooltip-effect="dark"
                      :border="border"
                      @selection-change="handleSelectionChange">
              <el-table-column type="selection" width="40"></el-table-column>
              <el-table-column prop="uid" label="UID" width="70"></el-table-column>
              <el-table-column prop="username" label="姓名" width="80"></el-table-column>
              <el-table-column prop="userpwd" label="密码" width="80"></el-table-column>
              <el-table-column prop="phone" label="手机号码" width="122" show-overflow-tooltip></el-table-column>
              <el-table-column prop="email" label="邮箱" width="310" show-overflow-tooltip></el-table-column>
              <el-table-column prop="address" label="地址" width="280" show-overflow-tooltip></el-table-column>

              <el-table-column label="操作" width="220">
                <template slot-scope="scope">
                  <!--handleEdit(scope.$index, scope.row)-->
                  <el-button type="primary" size="mini" @click="addTab(scope.$index,scope.row,1)">修改</el-button>
                  <el-button type="success" size="mini" @click="addTab(scope.$index,scope.row,2)">查看</el-button>
                  <el-button type="danger" size="mini" @click.native.prevent="deleteRow(scope.$index, tableData)">移除
                  </el-button>
                </template>
              </el-table-column>
            </el-table>
            <div style="margin-top: 20px" class="block">
              <el-pagination
                @size-change="handleSizeChange"
                @current-change="handleCurrentChange"
                :current-page="currentPage"
                :page-sizes="[5,10,20,30]"
                :page-size="pageSize"
                layout="total, sizes, prev, pager, next, jumper"
                :total="pageNum">
              </el-pagination>
            </div>
          </template>
        </div>
      </el-tab-pane>
      <el-tab-pane label="历史操作记录">
        <div class="">
          <div></div>
          <el-card class="box-card">
            <div slot="header" class="clearfix">
              <span>{{localUsers}}</span> 操作记录
            </div>
            <!-- v-for="o in 4" :key="o"
              {{'列表内容 ' + o }}-->
            <div class="text item">
              <template>
                <el-table
                  :data="history"
                  style="width: 100%"
                  :default-sort="{prop: 'date', order: 'descending'}">
                  <el-table-column
                    prop="date"
                    label="操作日期"
                    sortable
                    width="180">
                  </el-table-column>
                  <el-table-column
                    prop="uid"
                    label="UID"
                    sortable
                    width="180">
                  </el-table-column>
                  <el-table-column
                    prop="operate"
                    label="操作"
                    sortable
                    :formatter="formatter">
                  </el-table-column>
                </el-table>
              </template>
            </div>
          </el-card>
        </div>
      </el-tab-pane>
      <!--信息-->
      <el-tab-pane
        v-for="(item, index) in editableTabs"
        :key="index.name"
        :name="item.name"
        :label="item.target.username"
        :closable="closable">
        <div class="formClass">
          <el-form ref="form" :model="item.target" label-width="100px" :disabled="disabledAll">
            <el-form-item label="UID">
              <el-input v-model="item.target.uid" :disabled="disabled"></el-input>
            </el-form-item>
            <el-form-item label="姓名">
              <el-input v-model="item.target.username"></el-input>
            </el-form-item>
            <el-form-item label="密码">
              <el-input v-model="item.target.userpwd"></el-input>
            </el-form-item>
            <el-form-item label="手机号码">
              <el-input v-model="item.target.phone"></el-input>
            </el-form-item>
            <el-form-item label="邮箱">
              <el-input v-model="item.target.email"></el-input>
            </el-form-item>
            <el-form-item label="地址">
              <el-input v-model="item.target.address"></el-input>
            </el-form-item>
            <el-form-item>
              <el-button type="primary" @click="onSubmit(item.target.uid)">修改</el-button>
              <el-button @click="onCancel" v-show="!disabledAll">取消</el-button>
            </el-form-item>
          </el-form>
        </div>
      </el-tab-pane>
    </el-tabs>
  </el-container>
</template>

<script>
  // 获取当前管理员信息
  if(localStorage.getItem('user')){
    var localname = localStorage.getItem('user').split(",")[0].slice(2, -1);
  }
  export default {
    name: "ordinaryUser",
    data() {
      return {
        localUsers: localname,
        //标签页属性
        editableTabsValue: '0',
        editableTabs: [],
        tabIndex: 2,
        closable: true,
        // 表格属性
        border: true,
        //数据
        tableData: [],
        multipleSelection: [],
        // 表单数据对象
        disabled: true,
        disabledAll: false,
        //分页数据
        currentPage: 1,
        pageSize: 10,
        pageNum: 11,
        changeRows: 0,
        // 历史记录
        history: []
      }
    },
    //从本地服务器获取数据
    mounted(){
      var that_ = this;
      this.$axios.get('http://localhost:3000/db')
        .then(function (response) {
          that_.tableData = response.data.user
        })
        .catch(function (error) { // 请求失败处理
          console.log(error);
        });
    },
    methods: {
      //关闭选项卡
      removeTab(targetName) {
        let tabs = this.editableTabs;
        let activeName = this.editableTabsValue;
        if (activeName === targetName) {
          tabs.forEach((tab, index) => {
            if (tab.name === targetName) {
              let nextTab = tabs[index + 1] || tabs[index - 1];
              if (nextTab) {
                activeName = nextTab.name;
              }
            }
          });
        }
        this.editableTabsValue = activeName;
        this.editableTabs = tabs.filter(tab => tab.name !== targetName);
        // this.editableTabs = tabs.splice(targetName,1);
      },
      //标签页方法 在表格修改中使用 出现一个弹框
      addTab(index, rows, changes) {
        console.log(index + " rows:"+rows.uid)
        let newTabName = ++this.tabIndex + '';
        this.editableTabs.push({
          name: newTabName,
          target: this.tableData[(rows.uid-1)]
        });
        this.editableTabsValue = newTabName;
        if (changes === 1) {
          this.disabledAll = false
        } else if (changes === 2) {
          this.disabledAll = true
        }
      },
      handleSelectionChange(val) {
        this.multipleSelection = val;
      },
      // 移除表格数据
      deleteRow(index, rows) {
        //弹框
        this.$confirm('请确认是否删除 姓名：' + this.tableData[index].username + ' ', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          this.$message({
            type: 'success',
            message: '删除成功!'
          });
          //记录操作
          var dates = new Date();
          this.history.unshift({
            date: dates.getFullYear()+"-"+(dates.getMonth()+1)+"-"+dates.getDate()+"-"+dates.getHours()+":"+dates.getMinutes()+":"+dates.getSeconds(),
            uid: this.tableData[index].uid,
            operate: '删除'
          });
          //删除
          rows.splice(index, 1);
        }).catch(() => {
          this.$message({
            type: 'info',
            message: '已取消删除'
          });
        });
      },
      // 标签页方法
      onSubmit(mark) {
        this.$confirm('你确定要修改吗？', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          this.$message({
            type: 'success',
            message: '修改成功!'
          });

          //记录操作
          var dates = new Date();
          this.history.unshift({
            date: dates.getFullYear()+"-"+(dates.getMonth()+1)+"-"+dates.getDate()+"-"+dates.getHours()+":"+dates.getMinutes()+":"+dates.getSeconds(),
            uid: this.tableData[mark].uid,
            operate: '修改'
          });
          //修改原数据操作
          var i=0;i++;
          if (i==0){
            this.tableData.splice((mark-1),1,this.editableTabs[mark].target)
          }

        }).catch(() => {
          this.$message({
            type: 'info',
            message: '已取消修改'
          });
        });
      },
      onCancel() {
        this.editableTabsValue = "0"
      },
      //分页方法
      handleSizeChange(val) {
        this.pageSize = val;
        // console.log(`每页 ${val} 条`);
      },
      handleCurrentChange(val) {
        this.currentPage = val;
        // console.log(`当前页: ${val}`);
      },
      formatter(row, column, cellValue) {
        return row.operate;
      }
    },
    watch: {
      tableData: function (val) {
        this.pageNum = val.length
      }
    }
  }

</script>

<style>
  .tablist div:first-child span.el-icon-close {
    display: none;
  }

  .el-tabs--card > .el-tabs__header .el-tabs__nav {
    border: transparent;
  }

  .el-tabs {
    border: 1px solid #E4E7ED;
    border-radius: 4px;
  }

  .el-tabs__content {
    padding: 0 20px 20px;
  }

  /*表格属性*/
  .el-tabs__nav.is-top > div:last-child {
    border-right: 1px solid #E4E7ED;
  }

  /*姓名页属性*/
  .formClass {
    padding: 0 200px;
  }

  .el-form .el-form-item__content {
    width: 400px;
  }

  /*历史记录 卡片*/
  .box-card .text {
    font-size: 14px;
  }

  .box-card .item {
    margin-bottom: 18px;
  }

  .box-card .clearfix:before,
  .box-card .clearfix:after {
    display: table;
    content: "";
  }

  .box-card .clearfix:after {
    clear: both
  }

  .el-card {
    color: #909399;
    font-size: 14px;
  }

  .el-card span {
    color: #409EFF;
    font-size: 16px;
  }

  .el-card.is-always-shadow {
    box-shadow: 0 2px 6px 0 rgba(0, 0, 0, .1);
  }
</style>





