<template>
	<el-container>
		<el-main>
	<el-row class="row">
  <el-col :span="1"><div class="shuming">书名：</div></el-col>
  <el-col :span="4"><el-input v-model="input" placeholder="请输入书名"></el-input></el-col>
</el-row>
	<el-row class="row">
  <el-col :span="1"><div class="shuming">价格：</div></el-col>
  <el-col :span="4"><el-input v-model="input1" placeholder="请输入价格"></el-input></el-col>
</el-row>
	<el-row class="row">
  <el-col :span="1"><div class="shuming">介绍：</div></el-col>
  <el-col :span="4"><el-input v-model="input2" placeholder="请输入介绍"></el-input></el-col>
</el-row>
	<el-row class="row">
  <el-col :span="1"><div class="shuming">作者：</div></el-col>
  <el-col :span="4"><el-input v-model="input3" placeholder="请输入作者姓名"></el-input></el-col>
</el-row>
	<el-row class="row">
  <el-col :span="1"><div class="shuming">出版社：</div></el-col>
  <el-col :span="4"><el-input v-model="input4" placeholder="请输入出版社"></el-input></el-col>
</el-row>
<el-row class="row">
	<el-col :span="1"><div class="shuming">日期：</div></el-col>
	<el-col :span="6">
<div class="block">
    <el-date-picker
      v-model="value1"
      type="date"
      placeholder="选择日期">
    </el-date-picker>
 </div>
  </el-col>
  </el-row>
  <el-row>
	<el-button type="primary"  @click="open" class="button1">添加</el-button>
</el-row>
  </el-main>
</el-container>
</template>

<script>
	export default {
  data() {
    return {
      input: '',
      input1: '',
      input2: '',
      input3: '',
      input4: ''
    }
  },
   methods: {
      open() {
        this.$confirm('是否添加此书？', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          this.$message({
            type: 'success',
            message: '添加成功!'
          });
        }).catch(() => {
          this.$message({
            type: 'info',
            message: '已取消添加'
          });          
        });
      }
    },
    pickerOptions1: {
          disabledDate(time) {
            return time.getTime() > Date.now();
          },
          shortcuts: [{
            text: '今天',
            onClick(picker) {
              picker.$emit('pick', new Date());
            }
          }, {
            text: '昨天',
            onClick(picker) {
              const date = new Date();
              date.setTime(date.getTime() - 3600 * 1000 * 24);
              picker.$emit('pick', date);
            }
          }, {
            text: '一周前',
            onClick(picker) {
              const date = new Date();
              date.setTime(date.getTime() - 3600 * 1000 * 24 * 7);
              picker.$emit('pick', date);
            }
          }]
        },
        value1: '',
        value2: '',
};
</script>

<style>
	.shuming{
		padding-top: 8px;
	}
	.row{
		padding-top: 10px;
	}
	.button1{
		margin-left: 252px!important;
		margin-top: 10px!important;
	}
</style>


