<template>
  <el-container style="border: 1px solid grey;width: 1225px">
    <!--选项-->
    <div class="choose">
      <!--下单时间-->
      <div class="choose_row">
        <span class="choose_title">下单时间</span>
        <span v-for="(item , index) in SelectOne" @click="changeOne(index)" :key="index" class="option"
              :class="{colorOne:i===index}">
              {{ item.name }}
        </span>
        <select name="" >
          <option value="">2019-11-01至2015-11-02</option>
          <option value="">2019-11-02至2015-11-03</option>
        </select>
        <input type="checkbox" id="compare"/><label for="compare">对比</label>
        <select name="">
          <option value="">2019-11-01至2015-11-02</option>
          <option value="">2019-11-02至2015-11-03</option>
        </select>
      </div>
      <!--代理商名称-->
      <div class="choose_row">
        <span class="choose_title">代理商名称</span>
        <span v-for="(item , aa) in SelectTwo" @click="changeTwo(aa)" :key="aa" class="option"
              :class="{colorTwo:j===aa}">
              {{ item.name }}
        </span>
      </div>
      <!--图书类型-->
      <div class="choose_row">
        <span class="choose_title">图书类型</span>
        <select name="">
          <option value="" >运动</option>
          <option value="">杂志</option>
          <option value="">月刊</option>
          <option value="">小说</option>
        </select>
      </div>
      <!--类型-->
      <div class="choose_row">
        <span class="choose_title">类型</span>
        <span v-for="(item , index) in SelectThree" @click="changeThree(index)" :key="index" class="option"
              :class="{colorThree:r===index}">
        {{ item.name }}
        </span>
      </div>
    </div>
    <!--数据图-->
    <el-footer style="height: 410px">
      <router-view></router-view>
    </el-footer>
  </el-container>
</template>

<script>
export default {
  name: 'OrderStatistic',
  data () {
    return {
      SelectOne: [
        {name: '今日'},
        {name: '本周'},
        {name: '本月'}
      ],
      SelectTwo: [
        {name: '全部'},
        {name: '新华书店'},
        {name: '新建书碟'},
        {name: '新华书店'},
        {name: '新建书碟'},
        {name: '新华书店'},
        {name: '新建书碟'}
      ],
      SelectThree: [
        {name: '数量'},
        {name: '金额'}
      ],
      i: 0,
      j: 0,
      r: 0
    }
  },
  methods: {
    changeOne(index) {
      this.i = index
      if (index === 0) {
        this.$router.push({
          path: '/today'
        })
      }
      if (index === 1) {
        this.$router.push({
          path: '/this_week'
        })
      }
    },
    changeTwo(index) {
      console.log(this)
      this.j = index
      if (index === 0) {
        this.$router.push({
          path: '/today'
        })
      }
      if (index === 1) {
        this.$router.push({
          path: '/HelloWorld'
        })
      }
    },
    changeThree(index) {
      this.r = index
      if (index === 0) {
        this.$router.push({
          path: '/today'
        })
      }
      if (index === 1) {
        this.$router.push({
          path: '/this_week'
        })
      }
    }
  }
}
</script>

<style >
  .colorOne{
    color: blue;
  }
  .colorTwo{
    color: blue;
  }
  .colorThree{
    color: blue;
  }
  *{
    padding: 0;
    margin: 0;
  }
  .choose{
    background: rgb(247,247,247);
    width: 100%;
    padding: 20px 0;
  }
  .choose_title{
    width:  100px;
    text-align: right;
    padding-right: 10px;
    display: inline-block;
  }
  .option{
    border: 1px solid grey;
    padding: 1px 9px;
    background: white;
  }
  .choose_row{
    white-space: nowrap;
    height: 40px;
    line-height: 40px;
  }
  a:-webkit-any-link{
    text-decoration: none;
    color: black;
  }
  select{
    height: 24.8px;
    text-align: center;
    line-height: 24.8px;
    border: 1px solid grey;
    position: relative;
    top: -1.5px;
  }
</style>



