<template>
  <el-container>
    <el-tabs type="card" v-model="editableTabsValue" @tab-remove="removeTab" style="width: 100%">
      <el-tab-pane label="管理员用户">
        <div>
          <template>
            <el-table ref="multipleTable"
                      :data="tableData.slice((currentPage-1)*pageSize,currentPage*pageSize)"
                      tooltip-effect="dark"
                      :border="border"
                      @selection-change="handleSelectionChange">
              <el-table-column type="selection" width="40"></el-table-column>
              <el-table-column prop="username" label="姓名" width="80"></el-table-column>
              <el-table-column prop="userpwd" label="密码" width="80"></el-table-column>
              <el-table-column label="手机号码" width="120">暂未更新</el-table-column>
              <el-table-column label="邮箱" width="120">暂未更新</el-table-column>
              <el-table-column label="地址" width="120">暂未更新</el-table-column>
              <el-table-column label="操作" width="220">
                <template slot-scope="scope">
                  <!--handleEdit(scope.$index, scope.row)-->
                  <el-button type="primary" size="mini" :disabled="disabled">修改</el-button>
                  <el-button type="success" size="mini" @click="addTab(scope.$index,scope.row,2)">查看</el-button>
                  <el-button type="danger" size="mini" :disabled="disabled">移除</el-button>
                </template>
              </el-table-column>
            </el-table>
            <div style="margin-top: 20px" class="block">
              <el-pagination
                @size-change="handleSizeChange"
                @current-change="handleCurrentChange"
                :current-page="currentPage"
                :page-sizes="[5,10,20,30]"
                :page-size="pageSize"
                layout="total, sizes, prev, pager, next, jumper"
                :total="pageNum">
              </el-pagination>
            </div>
          </template>
        </div>
      </el-tab-pane>
      <el-tab-pane label="操作记录">
        <div class="">
          <el-card class="box-card">
            <div slot="header" class="clearfix">
              <span>{{localUsers}}</span> 操作记录
            </div>
            <div id="echart" style="display:inline-block;border:1px solid #EBEEF5;height: 480px;width: 500px;padding: 5px;"></div>
            <div id="echart2" style="display:inline-block;margin-left:50px;border:1px solid #EBEEF5;height: 480px;width: 700px;padding: 5px;"></div>
          </el-card>


        </div>
      </el-tab-pane>
    </el-tabs>
  </el-container>
</template>

<script>
  if (localStorage.getItem('user')){
    var localname = localStorage.getItem('user').split(",")[0].slice(2, -1);
  }
  export default {
    name: "administrations",
    data() {
      return {
        localUsers: localname,
        //标签页属性
        editableTabsValue: '0',
        editableTabs: [],
        tabIndex: 2,
        closable: true,
        // 表格属性
        border: true,
        //数据
        tableData: [],
        multipleSelection: [],
        // 表单数据对象
        disabled: true,
        //分页数据
        currentPage: 1,
        pageSize: 10,
        pageNum: 11,
        history: []
      }
    },
    mounted() {
      this.chart();
      this.chart2()
      var that_ = this;
      this.$axios.get('http://localhost:3000/db')
        .then(function (response) {
          that_.tableData = response.data.administrations
        })
        .catch(function (error) { // 请求失败处理
          console.log(error);
        });
    },
    methods: {
      //关闭选项卡
      removeTab(targetName) {
        let tabs = this.editableTabs;
        let activeName = this.editableTabsValue;
        if (activeName === targetName) {
          tabs.forEach((tab, index) => {
            if (tab.name === targetName) {
              let nextTab = tabs[index + 1] || tabs[index - 1];
              if (nextTab) {
                activeName = nextTab.name;
              }
            }
          });
        }
        this.editableTabsValue = activeName;
        this.editableTabs = tabs.filter(tab => tab.name !== targetName);
        // this.editableTabs = tabs.splice(targetName,1);
      },
      //标签页方法 在表格修改中使用 出现一个弹框
      addTab(index, rows, changes) {
        console.log(index + " rows:" + rows.uid)
        let newTabName = ++this.tabIndex + '';
        this.editableTabs.push({
          name: newTabName,
          target: this.tableData[(rows.uid - 1)]
        });
        this.editableTabsValue = newTabName;
        if (changes === 1) {
          this.disabledAll = false
        } else if (changes === 2) {
          this.disabledAll = true
        }
      },
      handleSelectionChange(val) {
        this.multipleSelection = val;
      },
      // 移除表格数据
      deleteRow(index, rows) {
        //弹框
        this.$confirm('请确认是否删除 姓名：' + this.tableData[index].username + ' ', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          this.$message({
            type: 'success',
            message: '删除成功!'
          });
          //记录操作
          var dates = new Date();
          this.history.unshift({
            date: dates.getFullYear() + "-" + (dates.getMonth() + 1) + "-" + dates.getDate() + "-" + dates.getHours() + ":" + dates.getMinutes() + ":" + dates.getSeconds(),
            uid: this.tableData[index].uid,
            operate: '删除'
          });
          //删除
          rows.splice(index, 1);
        }).catch(() => {
          this.$message({
            type: 'info',
            message: '已取消删除'
          });
        });
      },
      //分页方法
      handleSizeChange(val) {
        this.pageSize = val;
        // console.log(`每页 ${val} 条`);
      },
      handleCurrentChange(val) {
        this.currentPage = val;
        // console.log(`当前页: ${val}`);
      },
      chart: function () {
        // 基于准备好的dom，初始化echarts实例
        let main = this.$echarts.init(document.getElementById('echart'))
        // 绘制图表
        main.setOption({
          title : {
            text: '网站访问数量统计',
            x:'center'
          },
          color: ['#3398DB'],
          tooltip: {
            trigger: 'axis',
            axisPointer: {            // 坐标轴指示器，坐标轴触发有效
              type: 'shadow'        // 默认为直线，可选为：'line' | 'shadow'
            }
          },
          grid: {
            left: '3%',
            right: '4%',
            bottom: '3%',
            containLabel: true
          },
          xAxis: [
            {
              type: 'category',
              data: ['Mon', 'Tue', 'Wed', 'Thu', 'Fri', 'Sat', 'Sun'],
              axisTick: {
                alignWithLabel: true
              }
            }
          ],
          yAxis: [
            {
              type: 'value'
            }
          ],
          series: [
            {
              name: '直接访问',
              type: 'bar',
              barWidth: '60%',
              data: [10, 52, 200, 334, 390, 330, 220]
            }
          ]
        })
      },
      chart2: function () {
        // 基于准备好的dom，初始化echarts实例
        let main = this.$echarts.init(document.getElementById('echart2'));


        // 绘制图表
        var data = genData(50);
        main.setOption({
          title : {
            text: '登录网站次数统计',
            x:'center'
          },
          tooltip : {
            trigger: 'item',
            formatter: "{a} <br/>{b} : {c} ({d}%)"
          },
          legend: {
            type: 'scroll',
            orient: 'vertical',
            right: 10,
            top: 20,
            bottom: 20,
            data: data.legendData,
            selected: data.selected
          },
          series : [
            {
              name: '姓名',
              type: 'pie',
              radius : '55%',
              center: ['40%', '50%'],
              data: data.seriesData,
              itemStyle: {
                emphasis: {
                  shadowBlur: 10,
                  shadowOffsetX: 0,
                  shadowColor: 'rgba(0, 0, 0, 0.5)'
                }
              }
            }
          ]
        });
        function genData(count) {
          var nameList = [
            '王剑峰', '杨麒冬','邱文强','李杨','王虎','吕润虎'
          ];
          var legendData = [];
          var seriesData = [];
          var selected = {};
          for (var i = 0; i < 6; i++) {
            name = nameList[i]
            legendData.push(name);
            seriesData.push({
              name: name,
              value: Math.round(Math.random() * 100)
            });
            selected[name] = i < 6;
          }

          return {
            legendData: legendData,
            seriesData: seriesData,
            selected: selected
          };
        }
      }
    },
    watch: {
      tableData: function (val) {
        this.pageNum = val.length
      }
    }
  }
</script>

<style>
  .tablist div:first-child span.el-icon-close {
    display: none;
  }

  .el-tabs--card > .el-tabs__header .el-tabs__nav {
    border: transparent;
  }

  .el-tabs {
    border: 1px solid #E4E7ED;
    border-radius: 4px;
  }

  .el-tabs__content {
    padding: 0 20px 20px;
  }

  /*表格属性*/
  .el-tabs__nav.is-top > div:last-child {
    border-right: 1px solid #E4E7ED;
  }

  /*姓名页属性*/
  .formClass {
    padding: 0 200px;
  }

  .el-form .el-form-item__content {
    width: 400px;
  }

  /*历史记录 卡片*/
  .box-card .text {
    font-size: 14px;
  }

  .box-card .item {
    margin-bottom: 18px;
  }

  .box-card .clearfix:before,
  .box-card .clearfix:after {
    display: table;
    content: "";
  }

  .box-card .clearfix:after {
    clear: both
  }

  .el-card {
    color: #909399;
    font-size: 14px;
  }

  .el-card span {
    color: #409EFF;
    font-size: 16px;
  }

  .el-card.is-always-shadow {
    box-shadow: 0 2px 6px 0 rgba(0, 0, 0, .1);
  }
</style>



