<template>
<el-container style="width: 1250px;height: 600px">
<div class="sale_state" style="width: 1250px">
  <p style="text-align: center;padding: 10px 0;border-bottom:1px solid  rgb(64,158,255);color: rgb(64,158,255) ">书籍销售概况</p>
  <div style="width: 400px;border: 1px solid rgb(64,158,255);position:relative;top: 20px;height: 200px;float: left">
    <span class="title" style="color: rgb(64,158,255)">合约方式</span>
    <div style="width: 100%;border-bottom: 2px solid rgb(240,244,247);height: 60px">
      <span style="width: 50%;float: left;padding: 10px 0;">
        <p style="text-align: center;color: rgb(64,158,255)">247本</p>
        <p style="text-align: center;color: rgb(133,137,143)">当日</p>
      </span>
      <span style="width: 50%;float: left;padding: 10px 0;">
        <p style="text-align: center;color: rgb(64,158,255)">1895本</p>
        <p style="text-align: center;color: rgb(133,137,143)">当月</p>
      </span>
    </div>
    <div style="width: 100%;border-bottom: 2px solid rgb(240,244,247);height: 60px">
      <span style="width: 33.3%;float: left;padding: 10px 0;">
        <p style="text-align: center;line-height: 40px;color: rgb(133,137,143)">增长率</p>
      </span>
      <span style="width: 33.3%;float: left;padding: 10px 0;">
        <img src="../../assets/grow_rate.png" alt="" width="100%" height="100%">
      </span>
      <span style="width: 33.3%;float: left;padding: 10px 0;">
        <p style="text-align: center;line-height: 40px;color: rgb(30,105,187)">7.8%</p>
      </span>
      <p style="text-align: center;line-height: 40px">
        <span style="float:left;width: 50%;text-align: center;color: rgb(133,137,143)">本月环比增长率</span>
        <span style="float:left;width: 50%;color: rgb(0,110,43)">-7.5%↓</span>
      </p>
    </div>
  </div>
  <!--零售方式-->
  <div style="width: 400px;border: 1px solid rgb(64,158,255);position:relative;top: 20px;height: 200px;float: left;margin-left: 20px">
    <span class="title" style="color: rgb(64,158,255)">零售方式</span>
    <div style="width: 100%;border-bottom: 2px solid rgb(240,244,247);height: 60px">
      <span style="width: 50%;float: left;padding: 10px 0;">
        <p style="text-align: center;color: rgb(64,158,255)">247本</p>
        <p style="text-align: center;color: rgb(133,137,143)">当日</p>
      </span>
      <span style="width: 50%;float: left;padding: 10px 0;">
        <p style="text-align: center;color: rgb(64,158,255)">1895本</p>
        <p style="text-align: center;color: rgb(133,137,143)">当月</p>
      </span>
    </div>
    <div style="width: 100%;border-bottom: 2px solid rgb(240,244,247);height: 60px">
      <span style="width: 33.3%;float: left;padding: 10px 0;">
        <p style="text-align: center;line-height: 40px;color: rgb(133,137,143)">增长率</p>
      </span>
      <span style="width: 33.3%;float: left;padding: 10px 0;">
        <img src="../../assets/grow_rate.png" alt="" width="100%" height="100%">
      </span>
      <span style="width: 33.3%;float: left;padding: 10px 0;">
        <p style="text-align: center;line-height: 40px;color: rgb(30,105,187)">7.8%</p>
      </span>
      <p style="text-align: center;line-height: 40px">
        <span style="float:left;width: 50%;text-align: center;color: rgb(133,137,143)">本月环比增长率</span>
        <span style="float:left;width: 50%;color: red">43.5↑</span>
      </p>
    </div>
  </div>
  <div id="compare" style="float: left;width: 400px;margin-left: 26px;height: 202px;position: relative;top: 20px">
  </div>
  <div id="canal_one"  style="float: left;width: 615px;height: 300px;position: relative !important;top: 50px;"></div>
  <div id="canal_two"  style="float: left;width: 615px;height: 300px;position: relative !important;top: 50px;margin-left: 20px"></div>
</div>
</el-container>
</template>

<script>
export default {
  name: 'SaleStatistic',
  methods: {
    // 第一柱状图
    compare: function () {
      // 基于准备好的dom，初始化echarts实例
      let compare = this.$echarts.init(document.getElementById('compare'))
      // 绘制图表
      compare.setOption({
        tooltip: {
          trigger: 'item',
          formatter: '{a} <br/>{b} : {c} ({d}%)'
        },
        legend: {
          orient: 'vertical',
          left: 'left',
          data: ['零售方式', '合约方式']
        },
        series: [
          {
            name: '访问来源',
            type: 'pie',
            radius: '55%',
            center: ['50%', '60%'],
            data: [
              {value: 400, name: '零售方式'},
              {value: 1600, name: '合约方式'}

            ],
            itemStyle: {
              emphasis: {
                shadowBlur: 10,
                shadowOffsetX: 0,
                shadowColor: 'rgba(0, 0, 0, 0.5)'
              }
            }
          }
        ]
      })
    },
    canal_one: function () {
      // 基于准备好的dom，初始化echarts实例
      let canal_one = this.$echarts.init(document.getElementById('canal_one'))
      // 绘制图表
      canal_one.setOption({
        title:{
          text:'单位：本',
          left:'left',
          fontSize:'12px'
        },
        legend: {},
        tooltip: {},
        dataset: {
          dimensions: ['product', '所有方式', '合约方式', '零售方式'],
          source: [
            {product: '北京', '所有方式': 500, '合约方式': 345, '零售方式': 155},
            {product: '南宁', '所有方式': 450, '合约方式': 365, '零售方式': 85},
            {product: '重庆', '所有方式': 465, '合约方式': 350, '零售方式': 115},
            {product: '广州', '所有方式': 475, '合约方式': 320, '零售方式': 155}
          ]
        },
        xAxis: {type: 'category'},
        yAxis: {},
        // Declare several bar series, each will be mapped
        // to a column of dataset.source by default.
        series: [
          {type: 'bar'},
          {type: 'bar'},
          {type: 'bar'}
        ]
      })
    },
    canal_two: function () {
      // 基于准备好的dom，初始化echarts实例
      let canal_two = this.$echarts.init(document.getElementById('canal_two'))
      // 绘制图表
      canal_two.setOption({
        title:{
          text:'单位：本',
          left:'left',
          fontSize:'12px'
        },
        legend: {
        },
        tooltip: {},
        dataset: {
          dimensions: ['product', '所有渠道', '社会聚道', '自有渠道'],
          source: [
            {product: '北京', '所有渠道': 500, '社会聚道': 250, '自有渠道': 250},
            {product: '南宁', '所有渠道': 478, '社会聚道': 298, '自有渠道': 180},
            {product: '重庆', '所有渠道': 465, '社会聚道': 250, '自有渠道': 215},
            {product: '广州', '所有渠道': 475, '社会聚道': 235, '自有渠道': 240}
          ]
        },
        xAxis: {type: 'category'},
        yAxis: {},
        // Declare several bar series, each will be mapped
        // to a column of dataset.source by default.
        series: [
          {type: 'bar'},
          {type: 'bar'},
          {type: 'bar'}
        ]
      })
    }
  },
  mounted () {
    this.compare()
    this.canal_one()
    this.canal_two()
  }
}
</script>

<style>
  .title{
    display: inline-block;
    width: 80%;
    padding: 5px 0;
    text-align: center;
    margin: 0 10%;
    border-bottom: 1px solid rgb(64,158,255);
  }
</style>



