import Vue from 'vue'
import Router from 'vue-router'
import Login from '@/components/login'
import SystemHome from '@/components/system_home'
//用户管理
import UMordinaryUser from '@/components/usersM/ordinaryUser'
import UMadministrations from '@/components/usersM/administrations'
import UMaddUser from '@/components/usersM/addUser'
//订单管理
import Order from '@/components/orderM/order'
import OrderDfh from '@/components/orderM/orderDfh'
import OrderYfh from '@/components/orderM/orderYfh'
//图书管理
//    图书列表
import BookList from '@/components/booksM/xiugai'
//    推荐列表
import UserList from '@/components/booksM/userlist'
import NewList from '@/components/booksM/newList'
import ZhengJia from '@/components/booksM/zhengjia'
import CuXiao from '@/components/booksM/cuxiao'

//数据统计
import OrderStatistic from '@/components/dataStatistics/OrderStatistic'
import SaleStatistic from '@/components/dataStatistics/SaleStatistic'
import AgentSaleStatistic from '@/components/dataStatistics/AgentSaleStatistic'
import today from '@/components/dataStatistics/today'

import Test from "@/components/test"

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'login',
      component: Login
    },
    {
      path: '/system_home',
      name: '系统首页',
      component: SystemHome,
      children: [
        // 用户管理
        {
          path: '/user-ordinaryUser',
          name: '普通用户',
          component: UMordinaryUser
        },
        {
          path: '/user-administrations',
          name: "管理员用户",
          component: UMadministrations
        },
        {
          path: '/user-addUser',
          name: '添加用户',
          component: UMaddUser
        },
        // 订单管理
        {
          path: '/order',
          name: 'order',
          component: Order
        },
        {
          path: '/orderDfh',
          name: 'order',
          component: OrderDfh
        },
        {
          path: '/orderYfh',
          name: '订单统计',
          component: OrderYfh
        },
        // 图书管理
        {
          path: '/booksM-1',
          name: '图书列表',
          component: BookList
        },
        {
          path: '/booksM-2',
          name: '推荐列表',
          component: UserList
        },
        {
          path: '/booksM-3',
          name: '最新列表',
          component: NewList
        },
        {
          path: '/booksM-4',
          name: '添加图书',
          component: ZhengJia
        },
        {
          path: '/booksM-5',
          name: '促销列表',
          component: CuXiao
        },
        // 数据统计
        {
          path: '/OrderStatistic',
          name: '订单统计',
          component: OrderStatistic
        },
        {
          path: '/SaleStatistic',
          name: '销量统计',
          component: SaleStatistic
        },
        {
          path: '/AgentSaleStatistic',
          name: '代理商销量统计',
          component: AgentSaleStatistic
        },
        {
          path: '/*',
          name: 'test',
          component: Test
        },
      ]
    },
    {
      path: '/*',
      name: '登录',
      component: Login
    },

  ]
})



