<template>
<el-container>
	<el-main>
	<el-table
      :data="tableData"
      style="width: 100%">
      <el-table-column
        prop="id"
        label="编号"
        width="200">
      </el-table-column>
      <el-table-column
        prop="name"
        label="书名"
        width="200">
      </el-table-column>
       <el-table-column
        prop="zuozhe"
        label="作者"
        width="200">
      </el-table-column>
      <el-table-column
        prop="address"
        label="价格"
        width="200">
      </el-table-column>
         <el-table-column
        prop="jieshao"
        label="介绍"
        width="400">
      </el-table-column>
    </el-table>
    </el-main>
</el-container>
</template>
<style>
	.el-main{
		padding-left: 80px;
		padding-right: 80px;
	}
</style>
<script>
export default {
      data() {
        return {
          tableData: [{
            id: '78',
            name: '平凡的世界',
            zuozhe:'[中国] 路遥',
            address:'888',
            jieshao: '该书以中国70年代中期到80年代中期十年间为背景，通过复杂的矛盾纠葛，以孙少安和孙少平两兄弟为中心，刻画了当时社会各阶层众多普通人的形象；劳动与爱情、挫折与追求、痛苦与欢乐、日常生活与巨大社会冲突纷繁地交织在一起，深刻地展示了普通人在大时代历史进程中所走过的艰难曲折的道路'
          }, {
            id: '88',
            name: '时间简史',
            zuozhe:'[英] 斯蒂芬·威廉·霍金',
            address:'688',
            jieshao: '讲述了宇宙本性的最前沿知识，包括：我们的宇宙图像、空间和时间、膨胀的宇宙、不确定性原理、黑洞、宇宙的起源和命运等内容，深入浅出地介绍了遥远星系、黑洞、粒子、反物质等知识，并对宇宙的起源、空间和时间以及相对论等古老命题进行了阐述。'
          }, {
           id: '108',
            name: '穆斯林的葬礼',
            zuozhe:' [中国] 霍达',
            address:'998',
            jieshao: '一个穆斯林的家族，六十年间的兴衰，三代人命运的沉浮，两个发生在不同时代、有着不同内容却又交错扭结的爱情悲剧。揭示了他们在华夏文化和伊斯兰文化的撞击和融合中独特的心理结构，以及在政治、宗教的氛围中对人生真谛的困惑和追求。'
          }, {
            id: '888',
            name: '围城',
            zuozhe:'[中国] 钱钟书',
            address:'888',
            jieshao: '《围城》是中现代文学史上一部风格独特的讽刺小说。'
          }]
        }
      }
    }
</script>


